body {
    background-color: azure;
}

.CoreButton {
    height: 25px;
    max-height: 35px;
    white-space: nowrap;
    overflow: hidden;
}

.EndButton {
    width: 33%;
}

.MiddleButton {
    width: 34%;
}

.UnmatchedFields {
    width: 100%;
}

.App{
    margin: 20px;
}

.SectionMargin {
    margin-top: 20px;
}

.SortedFields {
    background-color: white;
    margin-left: 0;
    margin-right: 0;
    padding: 4px;
    border: 1px solid rgb(169, 169, 169);
}

.SortedField {
    display: table;
    margin: 0;
    padding: 0;
    cursor: pointer;
}

.SortedField:hover {
    -webkit-filter: brightness(90%);
            filter: brightness(90%);
}

.SortFieldCell {
    display: table-cell;
    text-align: left;
    vertical-align: middle;
    -ms-flex-align: center;
        align-items: center;
}

.GrabHandle {
    cursor: move;
    min-width: 20px;
    max-width: 20px;
}

.IsPatternIndicator {
    text-align: center;
    font-size: 10px;
    min-width: 30px;
}

.SortFieldText {
    width: 100%;
    white-space: nowrap;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    overflow: hidden;
    max-width: 1px;
}



